.header {
  border-bottom: 1px solid $headerBrBottom;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 88px;
  }

  &__burger {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: auto auto;
    background-color: $buttonBg;
    background-image: url("data:image/svg+xml,%3Csvg width='18' height='16' viewBox='0 0 18 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0 1C0 0.447715 0.447715 0 1 0H17C17.5523 0 18 0.447715 18 1C18 1.55228 17.5523 2 17 2H1C0.447716 2 0 1.55228 0 1ZM0 8C0 7.44772 0.447715 7 1 7H9C9.55228 7 10 7.44772 10 8C10 8.55228 9.55229 9 9 9H1C0.447715 9 0 8.55228 0 8ZM1 14C0.447715 14 0 14.4477 0 15C0 15.5523 0.447716 16 1 16H13C13.5523 16 14 15.5523 14 15C14 14.4477 13.5523 14 13 14H1Z' fill='white'/%3E%3C/svg%3E%0A");
  }

  &__logo {
    position: relative;
    margin-left: 17px;
    margin-right: auto;
  }

  @media (min-width: 768px) {
    &__logo {
      margin-left: 43px;
    }
    &__logo::before {
      content: "";
      position: absolute;
      left: -20px;
    }
  }

  @media (min-width: 1120px) {
    display: none;
  }
}

.consultation {
  &__list {
    display: flex;
  }

  &__list {
  }

  &__list-item {
  }

  &__list-item::before {
    content: "";
    position: absolute;
    left: -20px;
  }

  &__list-item + &__list-item {
    margin-left: 16px;
  }

  &__list-item:nth-child(4) {
    position: relative;
    margin-left: 42px;
  }

  @media (min-width: 768px) {
    &__list-item[hidden] {
      display: block;
    }
  }
  @media (min-width: 1120px) {
  }
}
